.alert{display:block;margin:0;padding:0;border:none;border-radius:0;background-color:#000}@media screen and (min-width: 320px) and (max-width: 480px){.alert .alertion-text .red p{display:flex;font-size:12px;margin:0;padding:0;margin-bottom:6px;justify-content:center;align-items:center}}@media screen and (min-width: 180px) and (max-width: 479px){.alert .alertion-text .red p{display:flex;font-size:10px;margin:0;padding:0;margin-bottom:6px;justify-content:center;align-items:center}}

.neon{background:-webkit-linear-gradient(101.19289deg, #4992f2 8.60417%, #4992f2 38.28125%, #00ede1 71.25579%, #00ede1 80.54861%);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.logo-neon{color:aqua}.logo-red{color:#f85b5b}.red{background:-webkit-linear-gradient(114.67196deg, #f93f48 5.73611%, #fab779 96.29167%);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.hr-red{border:2px solid red;padding:0;margin:0;z-index:-1}.hr-neon{border:1px solid aqua;padding:0;margin:0;z-index:-1}

.navbar{height:100px;margin:0;width:100%}.navbar .Navbar-logo{margin-right:30px}@media screen and (min-width: 360px) and (max-width: 480px){.navbar .Navbar-logo h1{font-size:36px}}.navbar .Navbar-searchbar{display:flex;flex:1 1}.navbar .Navbar-searchbar .Navbar-Input{width:100%;padding:5px 60px 5px 10px;outline:none;height:48px;border-bottom-left-radius:5px;border-top-left-radius:5px;color:#aaaaaa;font-size:14px;font-weight:700;background:rgba(228,227,227,0.7);border:2px solid rgba(228,227,227,0.7);border-right:none}.navbar .Navbar-searchbar .Navbar-Input::-webkit-input-placeholder{color:#aaaaaa}.navbar .Navbar-searchbar .Navbar-Input:-ms-input-placeholder{color:#aaaaaa}.navbar .Navbar-searchbar .Navbar-Input::placeholder{color:#aaaaaa}.navbar .Navbar-searchbar .Navbar-button{position:relative;background:rgba(228,227,227,0.7);outline:none;border:2px solid rgba(228,227,227,0.7);border-left:none;font-size:14px;right:0;border-bottom-right-radius:17px;border-top-right-radius:17px}.navbar .Navbar-searchbar .Navbar-button.transparent{background:transparent}.navbar .Navbar-nav{display:flex;justify-content:space-around;width:300px;margin-right:25px;text-align:center;margin-left:35px}.navbar .Navbar-nav .nav-box1{margin-left:5px;margin-right:5px;padding:0px 8px;cursor:pointer}.navbar .Navbar-nav .nav-box1:hover .nav-logo{color:orange;transition:0.6s all ease-in-out}@media screen and (min-width: 360px) and (max-width: 480px){.navbar .Navbar-nav .nav-box1{display:none}.navbar .Navbar-nav .nav-box1:nth-child(6){display:block}}@media screen and (min-width: 320px) and (max-width: 480px){.navbar .Navbar-nav .nav-box1{display:none}}@media screen and (min-width: 180px) and (max-width: 479px){.navbar .Navbar-nav .nav-box1{display:none}}@media screen and (min-width: 768px) and (max-width: 1025px){.navbar .Navbar-nav{align-items:center;display:flex;place-content:flex-end;margin-top:5px;margin-left:39px;margin-right:0px}}@media screen and (min-width: 768px) and (max-width: 1025px){.navbar .Navbar-nav .right-nav{display:none}}@media screen and (min-width: 1025px) and (max-width: 1600px){.navbar .Navbar-nav .right-nav{display:none}}

.nav-red{padding:8px 1px;margin:0;cursor:pointer;position:relative;font-size:15px;font-weight:bolder}.nav-red::before{content:'';position:absolute;width:100%;height:.3rem;background-color:red;left:0;bottom:0;-webkit-transform:scale(0);transform:scale(0);transition:0.4s all ease-in-out}.nav-red:hover::before{-webkit-transform:scale(0.9);transform:scale(0.9)}.nav-neon{padding:7px 1px;margin:0;cursor:pointer;position:relative;font-size:15px;font-weight:bolder}.nav-neon::before{content:'';position:absolute;width:100%;height:.3rem;background-color:aqua;left:0;bottom:0;-webkit-transform:scale(0);transform:scale(0);transition:0.4s all ease-in-out}.nav-neon:hover::before{-webkit-transform:scale(0.9);transform:scale(0.9)}ul{padding:0px}ul.active li{list-style:none;letter-spacing:0;padding:5px 12px;display:block;margin-bottom:.5px;font-weight:500;font-size:15px;cursor:pointer;background-size:200% 100%;background-position:left bottom;transition:0.4s all ease-in-out}ul.active li:hover{color:#fff;background:-webkit-linear-gradient(114.67196deg, #f93f48 5.73611%, #fab779 96.29167%);border-radius:0 66px 66px 0;-webkit-transform:translateX(-2%);transform:translateX(-2%)}.neon-ul{padding:0px}.neon-ul li{list-style:none;letter-spacing:0;padding:5px 12px;display:block;margin-bottom:.5px;font-weight:500;font-size:15px;cursor:pointer;background-size:200% 100%;background-position:left bottom;transition:0.4s all ease-in-out}.neon-ul li:hover{color:#fff;background:-webkit-linear-gradient(101.19289deg, #4992f2 8.60417%, #4992f2 38.28125%, #00ede1 71.25579%, #00ede1 80.54861%);border-radius:0 66px 66px 0;-webkit-transform:translateX(-2%);transform:translateX(-2%)}.shadow-light{box-shadow:2px 2px 10px red}.shadow-neon{box-shadow:2px 2px 10px aqua}

